<template>
  <transition name ="fade">
    <Modal :Rooms = "Rooms" :isShowModal = "isShowModal" :ciickIndex = "ciickIndex" @closeModal="isShowModal = false"/>
  </transition>
  <!--
  <div class = "start" :class="{end : isShowModal}">
    <Modal :Rooms = "Rooms" :isShowModal = "isShowModal" :ciickIndex = "ciickIndex" @closeModal="isShowModal = false"/>
  </div>-->
  <div class="menu">
    <a v-for="(a,i) in Menus" :key="i">{{a}}</a>
  </div>
  <Discount :discount_percent = "discount_percent" v-if="isShowDiscount == true"/>
  <button @click="priceSort">가격순정렬</button>
  <button @click="sortBack">되돌리기</button>
  <div v-for="(room,i) in Rooms" :key="i"> 
    <Oneroom :room = "room" @showModal="showDetail($event);" @increaseSingoCount="increaseSingoCount($event);"/>
  </div>
</template>

<script>

import {onerooms} from './assets/oneroom.js';
import Discount from './components/Discount.vue';
import Modal from './components/Modal.vue';
import Oneroom from './components/Oneroom.vue';

//alert(onerooms);
console.log("onerooms",onerooms);

export default {
  name: 'App',
  data() {
    return {
      isShowDiscount : true,
      ciickIndex : 0,
      isShowModal : false,
      Menus : ['Home','Shop','about'],
      Rooms : onerooms,
      RoomsOriginal : [...onerooms],   
      style_blue:'color:blue;',
      discount_percent: 30
    }
  },
  methods : {
    increaseSingoCount(index) {
      this.Rooms[index].singocount += 1
    },
    showDetail(index) {
      this.isShowModal = true;
      this.ciickIndex = index;
    },
    priceSort(){

      /*
      this.Rooms.sort(function(a,b){
        //return a.price-b.price
        return b.price-a.price
      });
      */
      // sort 하면 원본이 변형됨 
      this.Rooms.sort(function(a,b){
        //return a.price-b.price
        //return b.price-a.price

        if(a.title.toLowerCase() < b.title.toLowerCase())
          return -1;
        else if(a.title.toLowerCase() > b.title.toLowerCase())
          return 1; 
        else 
          return 0;   
      });

      /*
      var array = [3,5,2];
      array.sort(function(a,b){
        return a-b
      });
      */
    },
    sortBack() {
       this.Rooms= [...this.RoomsOriginal]; 
    }
  },
  beforeMount() {

  },
  mounted() {
    //setTimeout(()=>{
    //   this.isShowDiscount = false; 
    //},2000)
    var interval = setInterval(() => {
      this.discount_percent--;

      if(this.discount_percent < 1) {
        clearInterval(interval);
        this.isShowDiscount=false
      }
    },1000);
  },
  components: {
    Discount,
    Modal,
    Oneroom
}
}
</script>

