}
.end {
  opacity: 1;
}
.fade-enter-from{
   opacity: 0;
   transform: translateY(-1000px);
}
.fade-enter-active{
  transition: all 1s;
}
.fade-enter-to{
  opacity: 1;
  transform: translateY(0px);
}
